.tutorial-screen-container {
    display: flex;
    justify-content: center;
    align-items: center;
}
.tutorial-screen {
    width: 600px;
    min-height: 200px;
    margin: 0 auto;
    color: var(--white);
    display: flex;
    justify-content: center;
    align-items: stretch;
}

.tutorial-screen-image {
    width: 200px;
    height: 200px;
    background-position: center center;
    background-size: cover;
    margin-right: 30px;
}

@media screen and (max-width: 674px) {
    .tutorial-screen {
        width: 100%;
        padding: 0 16px;
        color: var(--white);
        flex-direction: column;
        align-items: center;
    }

    .tutorial-screen-image {
        margin-right: 0;
    }

    .tutorial-screen-text {
        margin-top: 16px;
    }
}

.tutorial-screen-image-0 {
    background-image: url(./tut_0.png);
}
.tutorial-screen-image-1 {
    background-image: url(./tut_1.png);
}
.tutorial-screen-image-2 {
    background-image: url(./tut_2.png);
}
.tutorial-screen-image-3 {
    background-image: url(./tut_3.png);
}

.tutorial-screen-text {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
}
.tutorial-screen-text p {
    width: 100%;
}
.tutorial-screen-text-filler {
    flex: 1;
    width: 100%;
    justify-content: center;
    align-items: stretch;
    flex-direction: column;
    display: flex;
}

.button.tutorial-screen-button {
    background: var(--red-2);
    border-radius: 6px;
    font-size: 18px;
    padding: 8px 16px;
    margin-top: 12px;
    width: 100%;
    max-width: 200px;
}
